import React from 'react'
import DooDoo from './DooDoo'
import {getDooDoo} from '../helpers'

class DooDooPicker extends React.Component {
  constructor(props) {
    super(props)
    this.state = {name: '', doodoos: []}
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleAddButtonClick = this.handleAddButtonClick.bind(this)
    this.handleRemoveButtonClick = this.handleRemoveButtonClick.bind(this)
  }

  handleSubmit(event) {
    event.preventDefault()
  }

  handleAddButtonClick(event) {
    event.preventDefault()

    this.setState({
      doodoos: ['', ...this.state.doodoos],
    })
    console.log(event)
  }

  handleRemoveButtonClick(event, index) {
    event.preventDefault()

    this.setState({
      doodoos: this.state.doodoos.filter((x, i) => i !== index),
    })
  }

  render() {
    const items = this.state.doodoos
    const last = items.length - 1

    return (
      <div>
        <button
          className="button-add bounce"
          onClick={this.handleAddButtonClick}
        >
          Add more Sandstorm
        </button>
        <div className="doodoo-container">
          <div className="doodoo-item">
            <p className="copy"> {getDooDoo()} </p>
          </div>
          {items.map((item, index) => (
            <div key={index} className="doodoo-item">
              <p className="copy"> {getDooDoo()} </p>
              <button
                className="button-remove"
                onClick={event => this.handleRemoveButtonClick(event, index)}
              >
                Remove
              </button>
            </div>
          ))}
        </div>
      </div>
    )
  }
}

export default DooDooPicker



