import React from 'react'
import DooDooPicker from './DooDooPicker'

class App extends React.Component {
  render() {
    return (
      <React.Fragment>
        <h1 className="header">Darude – Sandstorm ipsum</h1>
        <p>D:\A:\RUDE SANDSTORM </p>
        <DooDooPicker />
        <audio controls autoplay>
          <source src="/choons/darude-sandstorm.mp3" type="audio/mpeg" />
          Your browser does not support Darude.
        </audio>
      </React.Fragment>
    )
  }
}

export default App



